___CSS_LOADER_EXPORT___.push([module.id, "#tooltip[data-v-869f4154]{background:#333;border-radius:4px;color:#fff;font-size:13px;font-weight:700;padding:4px 8px}[data-v-869f4154] .popper{background:#333;border-radius:20px;color:#fff;font-weight:700;padding:2px;text-transform:uppercase}[data-v-869f4154] .popper #arrow:before{background:#333}[data-v-869f4154] .popper:hover,[data-v-869f4154] .popper:hover>#arrow:before{background:#333}", "",{"version":3,"sources":["webpack://./resources/js/Components/Tables/VideoTable.vue"],"names":[],"mappings":"AAiLA,0BACI,eAAgB,CAKhB,iBAAkB,CAJlB,UAAY,CAGZ,cAAe,CAFf,eAAiB,CACjB,eAGJ,CAEA,0BACI,eAAmB,CAEnB,kBAAmB,CACnB,UAAW,CACX,eAAiB,CAHjB,WAAY,CAIZ,wBACJ,CAEA,wCACI,eACJ,CAEA,8EAEI,eACJ","sourcesContent":["<template>\n\n    <div class=\"relative overflow-x-auto shadow-md sm:rounded-lg\">\n        <div\n            class=\"table w-full text-sm text-left text-gray-500 dark:text-gray-400\"\n        >\n            <div\n                class=\"table-header-group text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400\"\n            >\n                <div class=\"table-row\">\n                    <div scope=\"col\" class=\"table-cell px-6 py-3\">\n                        <font-awesome-icon icon=\"fa-repeat\" class=\"mr-2 cursor-pointer hover:text-blue-500\" @click.prevent=\"reload()\"/>\n                        Filename\n                    </div>\n                    <div scope=\"col\" class=\"hidden md:table-cell px-6 py-3\">\n                        Size\n                    </div>\n                    <div scope=\"col\" class=\"table-cell px-6 py-3\">\n                        Date\n                    </div>\n                    <div scope=\"col\" class=\"px-6 py-3\">\n\n                    </div>\n                </div>\n            </div>\n            <div class=\"table-row-group\">\n                <div\n                    v-for=\"video in videos.data\"\n                    :key=\"video.id\"\n                    class=\"table-row bg-white border-b dark:bg-gray-800 dark:border-gray-700\"\n                >\n                    <div\n                        scope=\"row\"\n                        class=\"table-cell min-w-[8rem] px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap\"\n                    >\n\n                        <Popper\n                            hover openDelay=\"50\" closeDelay=\"50\" offset-skid=\"0\" offset-distance=\"0\" placement=\"bottom\"\n\n                        ><template #content>\n                            <div class=\"text-xs\" id=\"tooltip\">\n                                <div v-if=\"video.file_name\">Filename: {{ video.file_name ? video.file_name : 'No File Name' }}</div>\n                                <div>ID: {{ video.id }}</div>\n                                <div>Type: {{ video.type }}</div>\n                                <div v-if=\"video.storage_location !== 'external'\">Size: {{ video.size }}</div>\n                                <div v-else>External</div>\n                                <div v-if=\"video.user_id\">Owner: {{ video.user_id}}</div>\n                                <div v-if=\"video.showEpisode?.show\">Show: {{ video.showEpisode.show.name}}</div>\n                                <div v-if=\"video.showEpisode\">Episode: {{ video.showEpisode.name}}</div>\n                                <div v-if=\"video.movie\">Movie: {{ video.movie.name}}</div>\n                                <div v-if=\"video.movieTrailer\">Trailer: {{ video.movieTrailer.name}}</div>\n                                <div v-if=\"video.newsPost\">News Post: {{ video.newsPost.name}}</div>\n                            </div>\n                        </template>\n\n\n                        <button\n                                v-if=\"video.can.view\"\n                                @click.prevent=\"playVideo(video)\"\n                                :disabled=\"video.upload_status === 'processing'\"\n                                class=\"disabled:cursor-not-allowed disabled:text-gray-500 disabled:italic inline\"\n                        >\n                            <div v-if=\"video.file_name\">{{ video.file_name.length > 15 ? video.file_name.substring(0, 15 - 3) + \"...\" : video.file_name.substring(0, 15) }}</div>\n                            <div v-else>{{video.storage_location}}</div>\n                        </button>\n                        </Popper>\n                        <div class=\"flex flex-row space-x-1\">\n                            <div v-if=\"video.showEpisode?.show\" class=\"w-fit text-xs rounded-lg px-1 uppercase bg-blue-800 text-white font-semibold hover:cursor-pointer\">\n                                Episode</div>\n                            <div v-if=\"video.movie\" class=\"w-fit text-xs rounded-lg px-1 uppercase bg-purple-800 text-white font-semibold hover:cursor-pointer\">\n                                Movie</div>\n                            <div v-if=\"video.movieTrailer\" class=\"w-fit text-xs rounded-lg px-1 uppercase bg-indigo-800 text-white font-semibold\">Trailer</div>\n                            <div v-if=\"video.newsPost\" class=\"hidden w-fit text-xs rounded-lg px-1 uppercase bg-orange-800 text-white font-semibold\">News</div>\n                            <div v-if=\"video.storage_location === 'external'\" class=\"hidden w-fit text-xs rounded-lg px-1 uppercase bg-blue-800 text-white font-semibold\">External</div>\n                        </div>\n                        <span v-if=\"!video.can.view\" class=\"font-semibold text-red-700\">You are currently unable to view this video. Please check with the admin.</span>\n                        <div v-show=\"video.upload_status === 'processing'\" class=\"ml-2 mb-1 py-1 px-2 bg-gray-600 text-gray-50 text-xs w-fit rounded-lg inline\">Processing</div>\n                    </div>\n                    <div\n                        scope=\"row\"\n                        class=\"hidden md:table-cell px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap\"\n                    >\n                        <span>{{ video.size }}</span>\n                    </div>\n                    <div class=\"table-cell px-6 py-4\">\n                        <span>{{ formatDate(video.created_at) }}</span>\n                    </div>\n                    <div>\n                        <Link :href=\"`#`\"><button\n                            @click.prevent=\"deleteVideo(video)\"\n                            class=\"px-3 py-2 mb-2 mr-4 font-xs text-white bg-red-600 hover:bg-red-500 rounded-lg\"\n                        >Delete</button>\n                        </Link>\n                    </div>\n\n                </div>\n            </div>\n        </div>\n        <!-- Paginator -->\n        <Pagination :data=\"videos\" class=\"pb-6\"/>\n    </div>\n\n</template>\n\n<script setup>\nimport Pagination from \"@/Components/Pagination.vue\";\nimport { useVideoPlayerStore } from \"@/Stores/VideoPlayerStore\";\nimport { useNowPlayingStore } from \"@/Stores/NowPlayingStore\";\nimport { useUserStore } from \"@/Stores/UserStore\";\nimport { Inertia } from \"@inertiajs/inertia\";\nimport { FontAwesomeIcon } from \"@fortawesome/vue-fontawesome\";\n\nconst videoPlayerStore = useVideoPlayerStore()\nconst nowPlayingStore = useNowPlayingStore()\nconst userStore = useUserStore()\n\nlet props = defineProps({\n    videos: Object,\n    can: Object,\n})\n\nconst playVideo = (video) => {\n    nowPlayingStore.reset()\n    // if file exists and is not processing, play file.\n    if (video.file_name !== '' && video.upload_status !== 'processing') {\n\n        // Check if showEpisode exists\n        if (video.showEpisode?.show) {\n            nowPlayingStore.show.name = video.showEpisode.show.name\n            nowPlayingStore.show.url = 'shows/'+video.showEpisode.show.slug\n            nowPlayingStore.show.episode.name = video.showEpisode.data.name\n            nowPlayingStore.show.episode.url = 'shows/'+video.showEpisode.show.slug+'/episode/'+video.showEpisode.data.slug\n        }\n        // Check if movie exists\n        if (video.movie) {\n            nowPlayingStore.movie.name = video.movie.name\n            nowPlayingStore.movie.url = 'movies/'+video.movie.slug\n        }\n\n        // Check if movieTrailer exists\n        if (video.movieTrailer) {\n            nowPlayingStore.movieTrailer.name = video.movieTrailer.name\n            nowPlayingStore.movieTrailer.url = 'movies/'+video.movieTrailer.slug\n        }\n        // Check if newsPost exists\n        if (video.newsPost) {\n            nowPlayingStore.newsPost.name = props.video.name\n            nowPlayingStore.movie.url = 'movies/'+video.movie.slug\n        }\n        videoPlayerStore.loadNewSourceFromFile(video)\n        nowPlayingStore.videoFile.name = video.file_name +' (file)'\n        videoPlayerStore.makeVideoFullPage()\n        videoPlayerStore.ott = 0\n        userStore.showNavDropdown = false\n        userStore.prevUrl = window.history.state.url\n        Inertia.visit('/stream')\n\n    }\n\n};\n\n\nfunction reload() {\n    Inertia.reload({\n        only: ['videos'],\n    });\n}\nfunction deleteVideo($video) {\n    if(confirm('Are you sure you want to delete this video? This action is not reversible and may have' +\n        ' devastating effects on the database.')) {\n        Inertia.post('/video/delete', {'videoId': $video.id});\n    }\n}\n\n</script>\n\n<style scoped>\n#tooltip {\n    background: #333;\n    color: white;\n    font-weight: bold;\n    padding: 4px 8px;\n    font-size: 13px;\n    border-radius: 4px;\n}\n\n:deep(.popper) {\n    background: #333333;\n    padding: 2px;\n    border-radius: 20px;\n    color: #fff;\n    font-weight: bold;\n    text-transform: uppercase;\n}\n\n:deep(.popper #arrow::before) {\n    background: #333333;\n}\n\n:deep(.popper:hover),\n:deep(.popper:hover > #arrow::before) {\n    background: #333333;\n}\n</style>\n"],"sourceRoot":""}]);
